.item {
  width: 350px;

  & p {
    margin: 6px 0px;
  }

  &__prices {
    display: flex;
    gap: 20px;
  
    &-new {
      color: #374151;
      font-size: 18px;
      font-weight: 600;
    }

    &-old {
      color: #8c8c8c;
      font-size: 18px;
      font-weight: 500;
      text-decoration: line-through;
    }
  }

  &:hover {
    transform: scale(1.05);
    transition: 0.6s;
  }
}